* {
  box-sizing: border-box;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  margin: 0;
}

body {
  margin: 0;
}

main {
  display: block;
}

ul {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

.redBtn {
  display: block;
  width: fit-content;
  padding: 16px 48px;
  background: #DB4444;
  color: #FFFFFF;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  text-decoration: none;
  border-radius: 4px;
  transition: all 0.3s;
}

.redBtn:hover {
  background: #fc4848;
}

.hoveredicon span,
.hoveredicon a {
  transition: 0.3s;
}

.hoveredicon svg path {
  transition: 0.3s;
}

.hoveredicon:hover svg path {
  stroke: #DB4444;
}

.hoveredicon:hover span {
  background: #DB4444 !important;
}

.hoveredicon.header__menu__item:hover a {
  color: #DB4444 !important;
}

.hoveredicon.header__menu__item:hover svg path {
  fill: #DB4444;
}

.container {
  max-width: 1234px;
  padding: 0px 32px;
  margin: 0 auto;
}

.top__header__section {
  background: #000000;
}

.top__header__section .container {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  min-height: 48px;
  gap: 231px;
}

.top__header__section .container .top__header__text p {
  color: #FAFAFA;
  font-size: 14px;
  margin: 0;
}

.top__header__section .container .top__header__text p a {
  margin-left: 8px;
  color: #FAFAFA;
  font-weight: 600;
}

.top__header__section .container .top__header__langs select {
  appearance: none;
  background: none;
  color: #fff;
  border: none;
  outline: 0;
  cursor: pointer;
}

.top__header__section .container .top__header__langs select option {
  appearance: none;
  background: #000000;
  border: none;
  cursor: pointer;
}

nav {
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.3019607843);
}

nav .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 40px;
  margin-bottom: 16px;
}

nav .container .nav__logo h2 a {
  color: #000000;
  text-decoration: none;
  font-size: 24px;
  font-family: "Inter", sans-serif;
  font-weight: 700;
}

nav .container .nav__menu {
  display: flex;
  gap: 48px;
}

nav .container .nav__menu .nav__menu__item a {
  position: relative;
  text-decoration: none;
  color: #000000;
  font-size: 16px;
}

nav .container .nav__menu .nav__menu__item a::before {
  content: "";
  width: 0%;
  height: 1px;
  background: rgba(0, 0, 0, 0.5019607843);
  position: absolute;
  left: 0;
  bottom: -5px;
  transition: 0.3s;
}

nav .container .nav__menu .nav__menu__item a:hover::before {
  width: 100%;
}

nav .container .nav__menu form {
  display: none;
}

nav .container .nav__icons {
  display: flex;
  align-items: center;
  gap: 24px;
}

nav .container .nav__icons form {
  position: relative;
}

nav .container .nav__icons form input {
  background: #F5F5F5;
  border-radius: 4px;
  padding: 7px 45px 7px 20px;
  border: none;
  outline: 0;
  width: 243px;
  height: 38px;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.5019607843);
}

nav .container .nav__icons form input::placeholder {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.5019607843);
}

nav .container .nav__icons form button {
  position: absolute;
  right: 12px;
  top: 8px;
  border: none;
  background: none;
  cursor: pointer;
}

nav .container .nav__icons .nav__icons__end {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 16px;
}

nav .container .nav__icons .nav__icons__end .to__wish__list__btn a,
nav .container .nav__icons .nav__icons__end .to__basket__btn a {
  display: block;
}

nav .container .nav__icons .nav__icons__end .to__wish__list__btn a svg,
nav .container .nav__icons .nav__icons__end .to__basket__btn a svg {
  display: block;
  width: 100%;
  height: 100%;
}

nav .container .nav__icons .nav__icons__end .burger__menu {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-between;
  width: 30px;
  height: 20px;
}

nav .container .nav__icons .nav__icons__end .burger__menu span {
  display: block;
  background: #000000;
  width: 100%;
  height: 2px;
  border-radius: 4px;
  transition: 0.3s;
}

nav .container .nav__icons .nav__icons__end .burger__menu.active span:nth-child(1) {
  transform: rotate(-45deg) translate(-11px, 2px);
}

nav .container .nav__icons .nav__icons__end .burger__menu.active span:nth-child(2) {
  display: none;
}

nav .container .nav__icons .nav__icons__end .burger__menu.active span:nth-child(3) {
  transform: rotate(45deg) translate(-11px, -2px);
}

header {
